.gallery-container {
	height: max-content;
	display: flex;
	justify-content: center;
	align-items: center;
}

.gallery-container .title-text {
	width: fit-content;
	margin-bottom: 20px;
}

.gallery-container .title-text h2 {
	font-weight: 600;
	letter-spacing: 1px;
	word-spacing: 5px;
	font-style: italic;
	margin: 0;
}

.gallery-container .title-text #photo-title {
	height: 3px;
	opacity: 1;
	background: #ffb800;
	margin-top: 3px;
}

.gallery-container .gallery-wrapper {
	padding: 40px 25px !important;
	display: grid;
	grid-template-columns: repeat(auto-fit, minmax(230px, 1fr));
	grid-gap: 15px;
	background: #030303de;
	border-radius: 5px;
}

.gallery-container .gallery-wrapper img {
	width: 100%;
	height: 100%;
	border-radius: 5px;
	border: 2px solid white;
	cursor: pointer;
}
